/**
 * An Activation that activates if a touch event is inside the selected activation split
 * The priority of the Activation helps prioritize between two overlaping Activation instances
 * @param {string} split - The selected activation split
 * @param {number} priority - The priority of the Activation
 */
const SplitActivationFactory = (split, priority) => {
  let activationSplit = SplitActivationFactory.RIGHT
  let priorityValue = 1
  let splitSize = 0.5

  if (split !== undefined) {
    activationSplit = split
  }

  if (priority !== undefined) {
    priorityValue = priority
  }

  /**
   * Returns true if the touch event is inside the selected activation split and false otherwise.
   * @param {Touch} touch - The current touch of the user 
   * @param {string} split - The selected activation split
   * @param {number} splitSize - The size of the split area as a fraction of the screen width and height
   */
  let splitTriggered = (touch, split, splitSize) => {
    const width = window.screen.width
    const height = window.screen.height
    switch(split) {
      case SplitActivationFactory.TOP:
        var y = height * splitSize
        if (touch.screenY < y) {
          return true
        }
        break
      case SplitActivationFactory.LEFT:
        var x = width * splitSize
        if (touch.screenX < x) {
          return true
        }
        break
      case SplitActivationFactory.RIGHT:
        var x = width * (1 - splitSize)
        if (touch.screenX >= x) {
          return true
        }
        break
      case SplitActivationFactory.BOTTOM:
        var y = height * (1 - splitSize)
        if (touch.screenY >= y) {
          return true
        }
        break
      default:
        return false
    }
    return false
  }

  return ({
    /**
     * Returns true if the Activation has been activated by the touch
     * @param {Touch} touch - The current touch of the user
     */
    isActivated: (touch) => {
      if (activationSplit === undefined) {
        return false
      }

      return splitTriggered(touch, activationSplit, splitSize)
    },

    /**
     * Sets the activation split
     * @param {string} split - The activation split
     */
    setActivationSplit: (split) => {
      if (side === SplitActivationFactory.TOP || 
        side === SplitActivationFactory.LEFT ||
        side === SplitActivationFactory.RIGHT ||
        side === SplitActivationFactory.BOTTOM) {
          activationSplit = split
      }
    },

    /**
     * Returns the priority of the action
     */
    getPriority: () => {
      return priorityValue
    },

    /**
     * Sets the priority of the action
     * @param {number} priority - The priority
     */
    setPriority: (priority) => {
      priorityValue = priority
    }
  });
};

// Supported splits
SplitActivationFactory.TOP = 'top'
SplitActivationFactory.LEFT = 'left'
SplitActivationFactory.RIGHT = 'right'
SplitActivationFactory.BOTTOM = 'bottom'

export const SplitActivation = SplitActivationFactory;
