/**
 * Returns bottom offset value + or - from viewport top
 * @param {DOMElement} el 
 * @param {*} i 
 */
function offsetBottom (el, i) { i = i || 0; return el.getBoundingClientRect().bottom + scrollOffset(el).pageYOffset }

/**
 * Returns right offset value
 * @param {DOMElement} el 
 * @param {*} i 
 */
function offsetRight (el, i) { i = i || 0; return el.getBoundingClientRect().right + scrollOffset(el).pageXOffset }

/**
 * Returns the current scroll offset
 * @param {DOMElement} element 
 */
function scrollOffset(element) {
  let win = element.ownerDocument.defaultView;
  return {
    pageXOffset: win.pageXOffset,
    pageYOffset: win.pageYOffset
  }
}

/**
 * Returns the offset if the given element
 * @param {DOMElement} element 
 */
function getOffset(element) {
  if (!element.getClientRects().length){
    return { top: 0, left: 0 };
  }

  let rect = element.getBoundingClientRect();
  let win = scrollOffset(element)
  return ({
    top: rect.top + win.pageYOffset,
    left: rect.left + win.pageXOffset
  });   
}

/**
 * Returns the distance from a location to a target
 * @param {DOMElement} target 
 * @param {number} currentX 
 * @param {number} currentY 
 */
function distanceToTarget(target, currentX, currentY) {
  var targetRect = target.getBoundingClientRect();
  var targetX = (targetRect.left+(targetRect.right-targetRect.left)/2) + scrollOffset(target).pageXOffset;
  var targetY = (targetRect.top+(targetRect.bottom-targetRect.top)/2) + scrollOffset(target).pageYOffset;
  return distanceBetween(targetX, targetY, currentX, currentY);
}

/**
 * converts percentage to Hex
 * @param {number} percent 
 */
function percentToHex(percent) {
  var hexValue = "00"
  if (percent < 7) {
      hexValue = "0"+percent.toString(16).toUpperCase();
  }
  else {
      hexValue = percent.toString(16).toUpperCase();
  }
  return hexValue
}

/**
 * Returns true if the touch intersects with the element
 * @param {DOMElement} element 
 * @param {number} touchX 
 * @param {number} touchY 
 */
export function hitTest(element, touchX, touchY) {
  let bounds = getOffset(element)
  bounds.right = offsetRight(element)
  bounds.bottom = offsetBottom(element)
  if (touchX >= bounds.left) {
    if (touchX <= bounds.right) {
      if (touchY >= bounds.top) {
        if (touchY <= bounds.bottom) {
          return true
        }
      }
    }
  }
  return false
}

/**
 * Returns the distance between two points
 * @param {number} targetX 
 * @param {number} targetY 
 * @param {number} currentX 
 * @param {number} currentY 
 */
export function distanceBetween(targetX, targetY, currentX, currentY) {
  var dx = targetX - currentX;
  var dy = targetY - currentY;
  var dist = Math.sqrt(dx * dx + dy * dy);
  return dist;
}

/**
 * Returns the horizontal distance between two points as a percentage of the screen width
 * @param {number} targetX 
 * @param {number} currentX 
 */
export function verticalDistanceInPercentOfScreenWidth(targetX, currentX) {
  var verticalDistance = targetX - currentX;
  return (verticalDistance / window.screen.width) * 100
}

/**
 * Returns the vertical distance between two points as a percentage of the screen height
 * @param {number} targetY 
 * @param {number} currentY 
 */
export function verticalDistanceInPercentOfScreenHeight(targetY, currentY) {
  var verticalDistance = targetY - currentY;
  return (verticalDistance / window.screen.height) * 100
}

/**
 * Sets the background color of the given element
 * @param {DOMElement} element 
 * @param {string} hexColor 
 */
export function setBackgroundColor(element, hexColor) {
  element.style.backgroundColor = hexColor;
}

/**
 * Sets the background color of the given element based on the distance between a touch and the target element
 * @param {DOMElement} element 
 * @param {string} baseColor 
 * @param {DOMElement} target 
 * @param {Touch} touch 
 */
export function setBackgroundColorBasedOnDistance(element, baseColor, target, touch) {
  const distance = distanceToTarget(target, touch.pageX, touch.pageY)
  const distanceInPercent = parseInt((distance / window.screen.height) * 100)
  const alpha = percentToHex(distanceInPercent)
  const color = baseColor + alpha
  element.style.backgroundColor = color;
}

/**
 * Returns true if the given element is neither null or undefined
 * @param {DOMElement} element 
 */
export function isElementAvailable(element) {
  if (element !== undefined && element !== null) {
    return true;
  }
  return false;
}

/**
 * Cnacels the given callback
 * @param {*} callback 
 */
export function cancelCallback(callback) {
  if (callback !== undefined) {
    clearTimeout(callback)
    callback = undefined
  }
  return callback
}
