/**
 * An Activation that activates if a touch event is inside the selected activation corner
 * The priority of the Activation helps prioritize between two overlaping Activation instances
 * @param {string} corner - The selected activation corner
 * @param {number} priority - The priority of the Activation
 */
const CornerActivationFactory = (corner, priority) => {
  let activationCorner = CornerActivationFactory.BOTTOM_RIGHT
  let priorityValue = 1
  let cornerBuffer = 0.2

  if (corner !== undefined) {
    activationCorner = corner
  }

  if (priority !== undefined) {
    priorityValue = priority
  }

  /**
   * Returns true if the touch event is inside the selected activation corner and false otherwise.
   * @param {Touch} touch - The current touch of the user 
   * @param {string} corner - The selected activation corner
   * @param {number} buffer - The size of the corner area as a fraction of the screen width and height
   */
  let cornerTriggered = (touch, corner, buffer) => {
    var x, y
    const width = window.screen.width
    const height = window.screen.height
    switch(corner) {
      case CornerActivationFactory.TOP_LEFT:
        x = width * buffer
        y = height * buffer
        if (touch.screenX < x && touch.screenY < y) {
          return true
        }
        break
      case CornerActivationFactory.TOP_RIGHT:
        x = width * (1 - buffer)
        y = height * buffer
        if (touch.screenX > x && touch.screenY < y) {
          return true
        }
        break
      case CornerActivationFactory.BOTTOM_LEFT:
        x = width * buffer
        y = height * (1 - buffer)
        if (touch.screenX < x && touch.screenY > y) {
          return true
        }
        break
      case CornerActivationFactory.BOTTOM_RIGHT:
        x = width * (1 - buffer)
        y = height * (1 - buffer)
        if (touch.screenX > x && touch.screenY > y) {
          return true
        }
        break
      default:
        return false
    }
    return false
  }

  return ({
    /**
     * Returns true if the Activation has been activated by the touch
     * @param {Touch} touch - The current touch of the user
     */
    isActivated: (touch) => {
      if (activationCorner === undefined) {
        return false
      }

      return cornerTriggered(touch, activationCorner, cornerBuffer)
    },

    /**
     * Sets the activation corner
     * @param {string} corner - The activation corner
     */
    setActivationCorner: (corner) => {
      if (corner === CornerActivationFactory.TOP_LEFT || 
        corner === CornerActivationFactory.TOP_RIGHT || 
        corner === CornerActivationFactory.BOTTOM_LEFT || 
        corner === CornerActivationFactory.BOTTOM_RIGHT) {
          activationCorner = corner
      }
    },

    /**
     * Sets the activation buffer as the fraction of the screen height/width giving the corner size
     * @param {*} buffer - The fraction of the screen height/width
     */
    setActivationBuffer: (buffer) => {
      cornerBuffer = buffer
    },

    /**
     * Returns the priority of the action
     */
    getPriority: () => {
      return priorityValue
    },

    /**
     * Sets the priority of the action
     * @param {number} priority - The priority
     */
    setPriority: (priority) => {
      priorityValue = priority
    },
    
  });
};

// Supported activation corners
CornerActivationFactory.TOP_LEFT = 'topLeft'
CornerActivationFactory.TOP_RIGHT = 'topRight'
CornerActivationFactory.BOTTOM_LEFT = 'bottomLeft'
CornerActivationFactory.BOTTOM_RIGHT = 'bottomRight'

export const CornerActivation = CornerActivationFactory;
