import * as util from "../util/util"

/**
 * Changes the color of the feedForward element based on the distance from the current touch to the targetElement
 * @param {DOMElement} feedForward - The element to change color
 * @param {string} baseColor - The base color for the color change
 * @param {string} intersectedColor - (optional) A color to be set if the targetElement is intersected
 */
const DistanceFeedForwardFactory = (feedForward, baseColor, intersectedColor) => {

  let targetElement = undefined
  let feedForwardElement = feedForward

  let feedForwardBaseColor = "#FF0000"
  let targetIntersectedColor = undefined
  let originalColor = "#FFFFFF"

  if (baseColor !== undefined) {
    feedForwardBaseColor = baseColor
  }

  if (intersectedColor !== undefined) {
    targetIntersectedColor = intersectedColor
  }

  return ({
    /**
     * Sets the feed forward element
     * @param {DOMElement} element - the feed forward element
     */
    setFeedForwardElement: (element) => {
      feedForwardElement = element
    },

    /**
     * Sets the base color for the color change
     * @param {string} color - The base color
     */
    setFeedForwardBaseColor: (color) => {
      feedForwardBaseColor = color
    },

    /**
     * Sets the intersection color to be set if the target element is intersected
     * @param {string} color - The intersection color
     */
    setTargetIntersectedColor: (color) => {
      targetIntersectedColor = color
    },

    /**
     * Sets the target element 
     * @param {DOMElement} element - The target element
     */
    _setTargetElement: (element) => {
      targetElement = element
    },

    /**
     * Handles the start event of a touch session
     * @param {Touch} touch - The current touch of the user
     */
    _touchStart: (touch) => {
      if (util.isElementAvailable(targetElement)) {
        if (util.isElementAvailable(feedForwardElement)) {
          try {
            originalColor = feedForwardElement.style.backgroundColor
          } catch(err) {
            console.log("ERROR! Failed to get original backgroundColor from feedForwardElement!")
          }
        }
      }
    },

    /**
     * Handles the move event during a touch session
     * @param {Touch} touch - The current touch of the user 
     */
    _touchMove: (touch) => {
      if (util.isElementAvailable(targetElement) && util.isElementAvailable(feedForwardElement)) {

        if (targetIntersectedColor !== undefined && util.hitTest(targetElement, touch.pageX, touch.pageY)) {
          util.setBackgroundColor(feedForwardElement, targetIntersectedColor)
        } else {
          util.setBackgroundColorBasedOnDistance(feedForwardElement, feedForwardBaseColor, targetElement, touch)
        }
      }
    },

    /**
     * Handles the end of a touch session
     * @param {Touch} touch - The current touch of the user 
     */
    _touchEnd: (touch) => {
      if (util.isElementAvailable(feedForwardElement)) {
        try {
          feedForwardElement.style.backgroundColor = originalColor
        } catch(err) {
          console.log("ERROR! Failed to reset feedForwardElement to the original backgroundColor!")
        }
      }
    }
  });
};

 export const DistanceFeedForward = DistanceFeedForwardFactory;
