var isPlaying = false;
var currentSound = undefined;

/**
 * Pauses the current sound
 */
function pauseSound() {
  if (currentSound !== undefined) {
    currentSound.pause()
  }
}

/**
 * Playes a sound
 * @param {AudioElement} sound - The sound to be played
 */
function playSound(sound) {
  if (sound !== undefined && sound !== null) {
    isPlaying = true;
    pauseSound()
    currentSound = sound;
    currentSound.currentTime = 0;
    currentSound.autoplay = true;
    currentSound.muted = false;
    var promise = currentSound.play()
    if (promise !== undefined) {
      promise.then(function() {}).catch(function(error) {
        console.log('Error playing sound...')
        console.log(error)
      });
    }
    currentSound.addEventListener("ended", function(){
      currentSound.currentTime = 0;
      isPlaying = false;
    });
  }
}

/**
 * Playes the given sound if another sound isn't already playing
 * @param {AudioElement} sound - The sound to be played
 */
export function play(sound) {
  if (isPlaying === false) {
    playSound(sound)
  }
}

/**
 * Playes the given sound even if another sound is already playing
 * @param {AudioElement} sound - The sound to be played
 */
export function forcePlay(sound) {
  playSound(sound)
}

/**
 * Stops any playing sound
 */
export function stop() {
  isPlaying = false;
  pauseSound()
}
