import * as vibrationManager from "./vibrationmanager"
import * as soundManager from "./soundmanager"

var active = false

var actions = []
var currentAction = undefined
var oldAction = undefined

/**
 * Handles touchstart events and delegates them to the correct Action
 */
document.addEventListener('touchstart', function(e) {
  if (active !== true) {
    return
  }
  const touch = e.touches[0]

  oldAction = undefined
  currentAction = getCurrentAction(touch)
  
  if (currentAction !== undefined) {
    currentAction._touchStart(touch)
  }
}, false);

/**
 * Handles touchmove events and delegates them to the correct Action
 */
document.addEventListener('touchmove', function(e) {
  if (active !== true || currentAction === undefined) {
    return
  }
  const touch = e.touches[0]

  if (currentAction.isLocked() !== true) {
    oldAction = currentAction
    currentAction = getCurrentAction(touch)
    if (oldAction !== currentAction) {
      oldAction._touchEnd(touch)
      currentAction._touchStart(touch)
    }
  }

  currentAction._touchMove(touch)
}, false);

/**
 * Handles touchend events and delegates them to the correct Action
 */
document.addEventListener('touchend', function(e) {
  if (active !== true || currentAction === undefined) {
    return
  }

  currentAction._touchEnd(e.touches[0])
  currentAction = undefined
  oldAction = undefined
}, false);

/**
 * Returns the currently activated Action
 * @param {Touch} touch - The current touch of the user
 */
function getCurrentAction(touch) {
  for (var i = 0; i < actions.length; i++) {
    const action = actions[i]
    if (action._isAvailable(touch)) {
      return action
    }
  }
}

/**
 * Returns the priority of an Action
 * @param {Action} action 
 */
function getPriority(action) {
  if (action !== undefined) {
    if (action.getPriority() !== undefined) {
      return action.getPriority()
    }
  }

  return -1
}

/**
 * Sorts (descending) actions based on their priority
 * @param {[Action]} actions 
 */
function sortActions(actions) {
  actions.sort((a, b) => (getPriority(a) < getPriority(b)) ? 1 : -1)
}

export const Manager = {
  /**
   * Sets the list of actions
   * @param {[Action]} actionList 
   */
  setActions: (actionList) => {
    actions = actionList
    sortActions(actions)
    currentAction = undefined
  },

  /**
   * Adds an Action to the list of actions
   * @param {Action} action 
   */
  addAction: (action) => {
    actions.push(action)
    sortActions(actions)
  },
  
  /**
   * Clears the list of actions
   */
  clearActions: () => {
    actions = []
    currentAction = undefined
  },
  
  /**
   * Activates the handling of touch events
   */
  start: () => {
    active = true
  },
  
  /**
   * Deactivates the handling of touch events
   */
  stop: () => {
    active = false
    soundManager.stop()
    vibrationManager.interrupt()
  },
  
  /**
   * Stops the handling of touch events and clears the list of actions
   */
  reset: () => {
    Manager.stop()
    Manager.clearActions()
    vibrationManager.stop()
  },
  
  /**
   * Plays a sound
   * @param {AudioElement} sound - The sound to be played
   * @param {boolean} forcePlay - Should interrupt if a sound is already being played
   */
  playSound: (sound, forcePlay) => {
    if (forcePlay === true) {
      soundManager.forcePlay(sound)
    } else {
      soundManager.play(sound)
    }
  },
  
  /**
   * returns true if the user has tapped the screen and enabled vibrations and sounds in the browser
   */
  isFeedbackActivated: () => {
    return vibrationManager.isVibrationActivated()
  }
};
