import * as util from "../util/util"
import * as vibrationManager from "../manager/vibrationmanager"
import * as soundManager from "../manager/soundmanager"

/**
 * An Action that triggeres a callback when the target element is intersected by the users touch.
 * The action also handles sound and vibration feedback based on the users touch input.
 * @param {DOMElement} target - The target element of the action.
 * @param {ActivationObject} activation - (optional) Object to handle activaion of action.
 * @param {feedforwardObject} feedForward - (optional) Object to handle feedforward .
 * @param {AudioElement} inSound - (optional) Audio element to be played when callback is triggered.
 * @param {function} callback - Callback to be triggered.
 */
const SimpleActionFactory = (target, activation, feedForward, inSound, callback) => {

  let targetElement = target
  let activationObject = activation
  let feedForwardObject = feedForward
  let intersectionSound = inSound

  let actionCallback = callback
  let actionDelay = 1000

  let vibrationPattern = [0,  85, 300,  85, 300,  85]

  let targetIntersected = false
  let intersectionCallback = undefined

  /**
   * Updates the FeedforwardObjects target element
   * @param {DOMElement} targetElement - The target element of Action
   */
  let updateFeedForwardObject = (targetElement) => {
    if (feedForwardObject !== undefined) {
      feedForwardObject._setTargetElement(targetElement)
    }
  }

  updateFeedForwardObject(targetElement)

  return ({
    /**
     * Sets the target element of the action
     * @param {DOMElement} element - The DOM element to be set as target
     */
    setTargetElement: (element) => {
      targetElement = element
      updateFeedForwardObject(targetElement)
    },

    /**
     * Sets the feedforward object on the action
     * @param {FeedforwardObject} feedForward - The object to be set as FeedforwardObject
     */
    setFeedForward: (feedForward) => {
      feedForwardObject = feedForward
      updateFeedForwardObject(targetElement)
    },

    /**
     * Sets the activation object on the action
     * @param {ActivationObject} activation - The object to be set as ActivationObject
     */
    setActivation: (activation) => {
      activationObject = activation
    },

    /**
     * Sets the interactionSound of the action
     * @param {AudioElement} sound - The Audio element to be set as interactionSound
     */
    setIntersectionSound: (sound) => {
      intersectionSound = sound
    },

    /**
     * Sets the action callback on the action
     * @param {function} callback - The function to be set as the actionCallback
     */
    setActionCallback: (callback) => {
      actionCallback = callback
    },

    /**
     * Sets the delay for when the callback is triggered when the target is intersectedd
     * @param {number} delay - The delay in miliseconds
     */
    setActionDelay: (delay) => {
      actionDelay = delay
    },

    /**
     * Sets the vibration pattern
     * @param {VibrationPattern} pattern - The vibration pattern
     */
    setVibrationPattern: (pattern) => {
      vibrationPattern = pattern
    },

    /**
     * Returns the priority of the action
     */
    getPriority: () => {
      if (activation !== undefined) {
        return activation.getPriority()
      }
      return -1
    },

    /**
     * Returns true if the users current touch session is locked to this action or false otherwise.
     */
    isLocked: () => {
      return true
    },
    
    /**
     * Returns true if the action is available for interaction for the current touch.
     * @param {Touch} touch - The current touch of the user 
     */
    _isAvailable: (touch) => {
      if (!util.isElementAvailable(targetElement)) {
        return false
      }
      if (activationObject === undefined) {
        return true
      }

      return activationObject.isActivated(touch)
    },

    /**
     * Handles the start event of a touch session
     * @param {Touch} touch - The current touch of the user
     */
    _touchStart: (touch) => {
      if (feedForwardObject !== undefined) {
        feedForwardObject._touchStart(touch)
      }
    },

    /**
     * Handles the move event during a touch session
     * @param {Touch} touch - The current touch of the user 
     */
    _touchMove: (touch) => {
      if (util.isElementAvailable(targetElement)) {
        if (feedForwardObject !== undefined) {
          feedForwardObject._touchMove(touch)
        }
        
        // Check if the touch has intersected the targetElement
        if (util.hitTest(targetElement, touch.pageX, touch.pageY)) {
          if (targetIntersected !== true) {
            targetIntersected = true
            if (actionCallback !== undefined && intersectionCallback === undefined) {
              intersectionCallback = window.setTimeout(() => {
                actionCallback(targetElement)
                intersectionCallback = undefined
              }, actionDelay)
            }
            
            vibrationManager.vibrate(vibrationPattern, true)
            soundManager.forcePlay(intersectionSound)            
          }
        }
        else {
          targetIntersected = false
          intersectionCallback = util.cancelCallback(intersectionCallback)
          vibrationManager.stop()
        }
      }
    },

    /**
     * Handles the end of a touch session
     * @param {Touch} touch - The current touch of the user 
     */
    _touchEnd: (touch) => {
      if (feedForwardObject !== undefined) {
        feedForwardObject._touchEnd(touch)
      }
      targetIntersected = false
      intersectionCallback = util.cancelCallback(intersectionCallback)
      vibrationManager.stop();
      soundManager.stop();
    }
  });
 };

 export const SimpleAction = SimpleActionFactory;
