var isVibrating = false;
var onVibrationEndCallback = undefined;
var vibrationActivated = false;
var interrupted = false

/**
 * Registers user interaction with the screen and thereby the activation of vibrations in the browser
 */
document.body.addEventListener('mouseup', function() {
  vibrationActivated = true;
}, true);

/**
 * Cancels callback for replaying vibration
 */
function cancelCallback() {
  clearTimeout(onVibrationEndCallback)
  onVibrationEndCallback = undefined
}

/**
 * Starts the given vibration
 * @param {VibrationPattern} pattern - The pattern to vibrate
 * @param {boolean} replayVibration - Should the patterne keep replaying
 */
export function vibrate(pattern, replayVibration) {
  if (window.navigator && onVibrationEndCallback === undefined && !isVibrating) {
    isVibrating = true
    const patternDuration = pattern.reduce((a, b) => a + b)

    const didVibrate = window.navigator.vibrate(pattern)
    if (didVibrate === true) {
      window.navigator.vibrate(pattern)
      vibrationActivated = true
    } else {
      vibrationActivated = false
      return;
    }
    
    onVibrationEndCallback = window.setTimeout(() => {
      if (replayVibration && isVibrating) {
        stop()
        if (interrupted === false) {
          vibrate(pattern, replayVibration)
        }
        interrupted = false
      } else {
        stop()
      }
    }, patternDuration);
  }
}

/**
 * Stop any vibrations
 */
export function stop() {
  isVibrating = false;
  cancelCallback()
  if (window.navigator) {
    window.navigator.vibrate(0)
  }
}

/**
 * Interrupt replaying vibration
 */
export function interrupt() {
  interrupted = true
}

/**
 * Returns true if vibrations has been activated by the user tapping the screen
 */
export function isVibrationActivated() {
  return vibrationActivated
}
