import * as util from "../util/util"
import * as vibrationManager from "../manager/vibrationmanager"
import * as soundManager from "../manager/soundmanager"


/**
 * An Action that triggeres a callback when the user scrolls on the specified directions.
 * The action also handles sound and vibration feedback based on the users touch input.
 * @param {string} scrollDirection - The direction/axis on wich the user can trigger the callback through scrolling
 * @param {ActivationObject} activation - (optional) Object to handle activaion of action.
 * @param {feedforwardObject} feedForward - (optional) Object to handle feedforward .
 * @param {function} callback - Callback to be triggered.
 */
const ScrollActionFactory = (scrollDirection, activation, feedForward, callback) => {
  let scrollDir = scrollDirection
  let activationObject = activation
  let feedForwardObject = feedForward

  let actionCallback = callback
  let actionDelay = 500

  let actionTriggered = false
  let triggeringTouch = undefined
  let compareTouch = undefined
  let vibrationCompareTouch = undefined
  let triggerCallback = undefined

  let vibrationPattern = [150, 50, 150, 50]

  /**
   * Handling the users movement/scroll when the action has been triggered
   * @param {Touch} touch - The current touch of the user
   */
  let handleMovement = (touch) => {
    if (compareTouch === undefined) {
      console.log('compareTouch initialized')
      compareTouch = touch
    } 
  
    if (vibrationCompareTouch === undefined) {
      console.log('vibrationCompareTouch initialized')
      vibrationCompareTouch = touch
    } 
    
    // We want to vibrate with a higher frequency then we trigger the callback
    const moveDistance = scrollDir == ScrollActionFactory.VERTICAL ? util.verticalDistanceInPercentOfScreenHeight(vibrationCompareTouch.pageY, touch.pageY) : util.verticalDistanceInPercentOfScreenWidth(vibrationCompareTouch.pageX, touch.pageX)
    if (moveDistance > .5 || moveDistance < -.5) {
      vibrationManager.vibrate([50, 10], false);
      vibrationCompareTouch = touch
    }
  
    // Trigger the callback if the scroll distance is larger than the threshold.
    const distance = scrollDir == ScrollActionFactory.VERTICAL ? util.verticalDistanceInPercentOfScreenHeight(compareTouch.pageY, touch.pageY) : util.verticalDistanceInPercentOfScreenWidth(compareTouch.pageX, touch.pageX)
    if (distance > 2.5 || distance < -2.5) {
      var moveDirection
      if (distance > 0) {
        moveDirection = scrollDir == ScrollActionFactory.VERTICAL ? ScrollActionFactory.MOVEMENT_UP : ScrollActionFactory.MOVEMENT_LEFT
      } else {
        moveDirection = scrollDir == ScrollActionFactory.VERTICAL ? ScrollActionFactory.MOVEMENT_DOWN : ScrollActionFactory.MOVEMENT_RIGHT
      }
      actionCallback(moveDirection)
      compareTouch = touch
    }
  }
  
  /**
   * Sets the callback that enables the scroll interactions
   * @param {Touch} touch - The current touch of the user
   */
  let setTriggerCallback = (touch) => {
    console.log("setTriggerCallback")
    triggeringTouch = touch

    triggerCallback = window.setTimeout(() => {
      vibrationManager.vibrate(vibrationPattern, false);
      actionTriggered = true
      compareTouch = undefined
      vibrationCompareTouch = undefined
    }, actionDelay);
  }

  /**
   * Cancels the triggering callback
   */
  let cancelTriggerCallback = () => {
    if (triggerCallback !== undefined) {
      clearTimeout(triggerCallback)
      triggerCallback = undefined
    }
  }

  return ({
    /**
     * Sets the scroll direction/axis on wich the user can trigger the callback through scrolling
     * @param {string} scrollDirection - The scroll direction/axis
     */
    setScrollDirection: (scrollDirection) => {
      if (side === ScrollActionFactory.VERTICAL || 
        side === ScrollActionFactory.HORIZONTAL) {
          scrollDir = scrollDirection
      }
    },

    /**
     * Sets the feedforward object on the action
     * @param {FeedforwardObject} feedForward - The object to be set as FeedforwardObject
     */
    setFeedForward: (feedForward) => {
      feedForwardObject = feedForward
    },

    /**
     * Sets the activation object on the action
     * @param {ActivationObject} activation - The object to be set as ActivationObject
     */
    setActivation: (activation) => {
      activationObject = activation
    },

    /**
     * Sets the action callback on the action
     * @param {function} callback - The function to be set as the actionCallback
     */
    setActionCallback: (callback) => {
      actionCallback = callback
    },

    /**
     * Sets the delay for when the scroll functionality is enabled
     * @param {number} delay - The delay in miliseconds
     */
    setActionDelay: (delay) => {
      actionDelay = delay
    },

    /**
     * Sets the vibration pattern
     * @param {VibrationPattern} pattern - The vibration pattern
     */
    setVibrationPattern: (pattern) => {
      vibrationPattern = pattern
    },

    /**
     * Returns the priority of the action
     */
    getPriority: () => {
      if (activation !== undefined) {
        return activation.getPriority()
      }
      return -1
    },

    /**
     * Returns true if the users current touch session is locked to this action or false otherwise.
     */
    isLocked: () => {
      return actionTriggered
    },

    /**
     * Returns true if the action is available for interaction for the current touch.
     * @param {Touch} touch - The current touch of the user 
     */
    _isAvailable: (touch) => {
      if (activationObject === undefined) {
        return true
      }

      return activationObject.isActivated(touch)
    },
    
    /**
     * Handles the start event of a touch session
     * @param {Touch} touch - The current touch of the user
     */
    _touchStart: (touch) => {
      if (feedForwardObject !== undefined) {
        feedForwardObject._touchStart(touch)
      }

      setTriggerCallback(touch)
    },

    /**
     * Handles the move event during a touch session
     * @param {Touch} touch - The current touch of the user 
     */
    _touchMove: (touch) => {
      if (feedForwardObject !== undefined) {
        feedForwardObject._touchMove(touch)
      }

      if (triggeringTouch !== undefined) {
        if (actionTriggered === true) {
          handleMovement(touch);
        } else {
          const distanceToTrigger = util.distanceBetween(triggeringTouch.pageX, triggeringTouch.pageY, touch.pageX, touch.pageY)
          if (distanceToTrigger > 100) {
            cancelTriggerCallback()
            setTriggerCallback(touch)
          }
        }
      } else {
        cancelTriggerCallback()
        setTriggerCallback(touch)
      }
    },

    /**
     * Handles the end of a touch session
     * @param {Touch} touch - The current touch of the user 
     */
    _touchEnd: (touch) => {
      if (feedForwardObject !== undefined) {
        feedForwardObject._touchEnd(touch)
      }

      actionTriggered = false
      cancelTriggerCallback()
      vibrationManager.stop();
      soundManager.stop();
    }
  });
};

// Supported scroll/movement axis
ScrollActionFactory.VERTICAL = 'vertical'
ScrollActionFactory.HORIZONTAL = 'horizontal'

// Supported scroll/mevement directions
ScrollActionFactory.MOVEMENT_UP = 'movementUp'
ScrollActionFactory.MOVEMENT_DOWN = 'movementDown'
ScrollActionFactory.MOVEMENT_LEFT = 'movementLeft'
ScrollActionFactory.MOVEMENT_RIGHT = 'movementRight'

export const ScrollAction = ScrollActionFactory;
